import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Utils } from '../../../providers/Utils';
import { HttpServicesProvider } from '../../../providers/http-services';
/**
 * Generated class for the SearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-search',
  templateUrl: 'search.html',
})
export class SearchPage {
  public description='';
  public list=[];
  constructor(public navCtrl: NavController, public navParams: NavParams,public httpService: HttpServicesProvider) {
    
  }
  search(){
    let url = this.navParams.get('url');
    if(this.description != ''){
    var fuzzysearch =   encodeURIComponent(this.description)
    var $url = url+fuzzysearch;
      this.initlist($url)
    }
  }
  initlist(url){//初始化列表
    var that = this;
    that.httpService.get(url, null, (result) => {
      if (result != null && result.rows.length > 0) {
        var rows = result.rows;
        console.log(rows)
        for(var i=0;i<rows.length;i++){
          if(rows[i].acceptime != '' && rows[i].acceptime != null){
            rows[i].acceptime = Utils.dateFormat(new Date(rows[i].acceptime ), 'yyyy-MM-dd');
          }
        }
        that.list = rows;
        // this.maxpage = result.total;
        // this.result = result;
       
      }
   
    });
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchPage');
  }

}



